import React, { useState } from "react";
import { ConfigEnv, setConfigEnv } from "../../config";
import healthProvider from "../../providers/healthProvider";
import styles from "./environmentSelection.module.css";

let lastRefresh = 0;
const REFRESH_INTERVAL = 60 * 1000;

const EnvironmentSelection: React.FC = () => {
  const [env, setEnv] = useState();

  async function findBestEnvironment() {
    const now = Date.now();
    if (lastRefresh + REFRESH_INTERVAL > now)
      return setTimeout(findBestEnvironment, 1000);
    lastRefresh = now;

    const envPriority: ConfigEnv[] = ["dev", "local", "prod"];
    let foundEnv = false;
    for (let i = 0; i < envPriority.length; i++) {
      try {
        const env = envPriority[i];
        await healthProvider.healthcheck(env);
        setEnv(env);
        setConfigEnv(env);
        foundEnv = true;
        break;
      } catch (e) {}
    }
    if (!foundEnv) {
      setEnv("offline");
      setConfigEnv("prod");
    }

    setTimeout(findBestEnvironment, 1000);
  }

  if (!env) {
    findBestEnvironment();
    return null;
  }

  const color =
    env === "prod"
      ? styles.blue
      : env === "local"
      ? styles.green
      : env === "offline"
      ? styles.red
      : env === "dev"
      ? styles.black
      : "";

  return <div className={[styles.circle, color].join(" ")} />;
};

export default EnvironmentSelection;
