import React, { useState } from "react";
import Header from "../../components/header/header";
import styles from "./devices.module.css";
import Toggle from "../../components/toggle/toggle";
import deviceProvider, { Device } from "../../providers/deviceProvider";

const Devices: React.FC = () => {
  const [deviceList, setDeviceList] = useState([] as Device[]);

  if (!deviceList.length) {
    deviceProvider.getAll().then(setDeviceList);
  }

  const setValue = (id: string) => (status: any) => {
    const idx = deviceList.findIndex(d => d.id === id);
    const device = { ...deviceList[idx] };
    device.status = status;
    const list = deviceList.slice();
    list.splice(idx, 1, device);
    deviceProvider.put(device.id, { status });
    setDeviceList(list);
  };

  return (
    <React.Fragment>
      <Header />
      <div className={styles.container}>
        {deviceList.map(device => (
          <div key={device.id} className={styles.row}>
            <div className={styles.left}>{device.name}</div>
            <div className={styles.right}>
              {typeof device.status === "boolean" ? (
                <Toggle value={device.status} onChange={setValue(device.id)} />
              ) : null}
            </div>
          </div>
        ))}
      </div>
    </React.Fragment>
  );
};

export default Devices;
