import config from "../config";
import authHelper from "../utils/authHelper";

class BeaconProvider {
  async currentDevices(): Promise<string> {
    const token = authHelper.getToken();
    if (token === null) throw new Error("UNAUTHORIZED");
    const res = await fetch(
      `${config("serverPath")}/api/nearby_devices/current`,
      {
        headers: { Authorization: token }
      }
    );
    const msg = await res.text();
    return msg;
  }
  async allDevices(): Promise<string> {
    const token = authHelper.getToken();
    if (token === null) throw new Error("UNAUTHORIZED");
    const res = await fetch(`${config("serverPath")}/api/nearby_devices/all`, {
      headers: { Authorization: token }
    });
    const msg = await res.text();
    return msg;
  }
}

const beaconProvider = new BeaconProvider();

export default beaconProvider;
export { BeaconProvider };
