import React from "react";
import styles from "./input.module.css";

interface PropTypes<T extends string | number> {
  onChange: { (newVal: T): unknown };
  value: T;
}

const Input: React.FC<PropTypes<string | number>> = props => {
  const parseOnChange = (val: string) => {
    if (typeof props.value === "number") return parseFloat(val);
    return val;
  };
  return (
    <input
      type="text"
      className={styles.container}
      onChange={e => props.onChange(parseOnChange(e.target.value))}
      value={props.value}
    />
  );
};

export default Input;
