import React, { useState } from "react";
import Header from "../../components/header/header";
import style from "./gateOpener.module.css";
import gateProvider from "../../providers/gateProvider";

const GateOpener: React.FC = () => {
  const [logs, setLogs] = useState("");
  if (!logs) gateProvider.getLogs().then(setLogs);

  const [openRes, setOpenRes] = useState("");
  return (
    <React.Fragment>
      <Header />
      <div className={style.container}>
        <input
          type="submit"
          value="Open Gate"
          onClick={() => gateProvider.openGate().then(setOpenRes)}
        />
        <div>{openRes}</div>
        <div className={style.logs}>
          {logs
            .split("\n")
            .reverse()
            .join("\n")}
        </div>
      </div>
    </React.Fragment>
  );
};

export default GateOpener;
