import React from "react";
import style from "./login.module.css";
import authProvider from "../../providers/authProvider";
import authHelper from "../../utils/authHelper";
import { Redirect } from "react-router";
import config from "../../config";

interface LoginProps {}
interface LoginState {
  loggedIn: boolean;
  username: string;
  password: string;
}

class Login extends React.Component<LoginProps, LoginState> {
  state = {
    loggedIn: false,
    username: "",
    password: ""
  };
  login = async (e: React.FormEvent) => {
    e.preventDefault();
    const auth = await authProvider.login(
      this.state.username,
      this.state.password
    );
    authHelper.setAuth(auth);
    this.setState({});
  };
  render() {
    if (authHelper.getToken() !== null)
      return <Redirect to={config("appRoot")} />;
    return (
      <div className={style.container}>
        <form onSubmit={this.login}>
          <div className={style.header}>Login</div>
          <input
            placeholder="Username"
            type="text"
            name="username"
            onChange={e => this.setState({ username: e.target.value })}
          />
          <input
            placeholder="Password"
            type="password"
            name="password"
            onChange={e => this.setState({ password: e.target.value })}
          />
          <input className={style.submit} type="submit" name="submit" />
        </form>
      </div>
    );
  }
}

export default Login;
