import config from "../config";
import authHelper from "../utils/authHelper";

export interface Device {
  id: string;
  name: string;
  status: boolean | null;
}

class DeviceProvider {
  async getAll(): Promise<Device[]> {
    const token = authHelper.getToken();
    if (token === null) throw new Error("UNAUTHORIZED");
    const res = await fetch(`${config("serverPath")}/api/devices`, {
      headers: {
        Authorization: token
      }
    });
    const msg = await res.json();
    return msg.devices;
  }

  async put(id: string, device: Partial<Device>): Promise<Device> {
    const token = authHelper.getToken();
    if (token === null) throw new Error("UNAUTHORIZED");
    const res = await fetch(`${config("serverPath")}/api/devices/${id}`, {
      method: "PUT",
      headers: {
        Authorization: token,
        "Content-Type": "application/json"
      },
      body: JSON.stringify(device)
    });
    const msg = await res.json();
    return msg;
  }
}

const deviceProvider = new DeviceProvider();

export default deviceProvider;
export { DeviceProvider };
