import config from "../config";
import authHelper from "../utils/authHelper";

class GateProvider {
  async openGate(): Promise<string> {
    const token = authHelper.getToken();
    if (token === null) throw new Error("UNAUTHORIZED");
    const res = await fetch(`${config("serverPath")}/api/keypad/open`, {
      method: "POST",
      headers: {
        Authorization: token // "Bearer " + authHelper.getToken()
      }
    });
    const msg = await res.text();
    return msg;
  }
  async getLogs(): Promise<string> {
    const token = authHelper.getToken();
    if (token === null) throw new Error("UNAUTHORIZED");
    const res = await fetch(`${config("serverPath")}/api/keypad/logs`, {
      method: "GET",
      headers: {
        Authorization: token // "Bearer " + authHelper.getToken()
      }
    });
    const msg = await res.text();
    return msg;
  }
}

const gateProvider = new GateProvider();

export default gateProvider;
export { GateProvider };
