import React, { useState } from "react";
import Header from "../../components/header/header";
import beaconProvider from "../../providers/beaconProvider";

const NearbyDevices: React.FC = () => {
  const [nearbyDevices, setNearbyDevices] = useState("");
  if (!nearbyDevices) {
    beaconProvider
      .currentDevices()
      .then(setNearbyDevices)
      .catch(console.error.bind(console));
  }
  return (
    <React.Fragment>
      <Header />
      <h1>Nearby Devices</h1>
      <div id="current" dangerouslySetInnerHTML={{ __html: nearbyDevices }} />
      <h1>All Devices</h1>
      <div id="all" />
    </React.Fragment>
  );
};

export default NearbyDevices;
